.status-upload{
	background: none repeat scroll 0 0 #f5f5f5;
	-webkit-border-radius: 4px;
	-moz-border-radius: 4px;
	-ms-border-radius: 4px;
	-o-border-radius: 4px;
	border-radius: 4px;
	float: left;
	width: 100%;

	form{
		float: left;
		width: 100%;

		textarea{
			background: none repeat scroll 0 0 #fff;
			border: medium none;
			-webkit-border-radius: 4px 4px 0 0;
			-moz-border-radius: 4px 4px 0 0;
			-ms-border-radius: 4px 4px 0 0;
			-o-border-radius: 4px 4px 0 0;
			border-radius: 4px 4px 0 0;
			color: #777777;
			float: left;
			font-family: Lato;
			font-size: 14px;
			height: 142px;
			letter-spacing: 0.3px;
			padding: 20px;
			width: 100%;
			resize:vertical;
			outline:none;
			border: 1px solid #F2F2F2;
		}

		button{
			border: medium none;
			-webkit-border-radius: 4px;
			-moz-border-radius: 4px;
			-ms-border-radius: 4px;
			-o-border-radius: 4px;
			border-radius: 4px;
			color: #fff;
			float: right;
			font-family: Lato;
			font-size: 14px;
			letter-spacing: 0.3px;
			margin-right: 9px;
			margin-top: 9px;
			padding: 6px 15px;

			>i{
				margin-right: 7px;
			}
		}
	}

	ul{
		float: left;
		list-style: none outside none;
		margin: 0;
		padding: 0 0 0 15px;
		width: auto;

		>li{
			float: left;
			>a{
				-webkit-border-radius: 4px;
				-moz-border-radius: 4px;
				-ms-border-radius: 4px;
				-o-border-radius: 4px;
				border-radius: 4px;
				color: #777777;
				float: left;
				font-size: 14px;
				height: 30px;
				line-height: 30px;
				margin: 10px 0 10px 10px;
				text-align: center;
				-webkit-transition: all 0.4s ease 0s;
				-moz-transition: all 0.4s ease 0s;
				-ms-transition: all 0.4s ease 0s;
				-o-transition: all 0.4s ease 0s;
				transition: all 0.4s ease 0s;
				width: 30px;
				cursor: pointer;

				&:hover{
					background: none repeat scroll 0 0 #606060;
					color: #fff;
				}
