// tree
.tree-structure{
	list-style: none;
	clear: both;
}
.tree-structure li {
	position: relative;
}
.tree-structure li label{
	font-weight: normal;
	color: #fff;
	text-decoration: none;
	font-weight: 700;
	vertical-align: middle;
	-webkit-transition: all 0.5s ease-in-out;
	-moz-transition: all 0.5s ease-in-out;
	-ms-transition: all 0.5s ease-in-out;
	-o-transition: all 0.5s ease-in-out;
	transition: all 0.2s ease-in-out;
	display: inline-block;
	max-width: calc(100% - 50px);
	vertical-align: top;
}
.tree-structure li label:hover{
	padding-left: 5px;
}
.tree-structure > li > .check{
	display: inline-block;
	background: #333;
	min-width: 24px;
	padding-left: 0px;
	padding-right: 0px;
	text-align: center;
	padding: 3px 9px;
	margin-right: 10px;
	color: #fff;
	font-weight: 700;
	font-size: 12px;
}
.tree-structure > li > .check:after{
	position: absolute;
	content: "";
	width: 1px;
	height: 100%;
	background-color: #939393;
	top: 5px;
	left: 12px;
	z-index: -1;
}
.tree-structure > li:last-child > .check:after{	
	height: calc(100% - 44px);
}
.tree-structure ol{
	padding: 20px 0 20px 45px;
}
.tree-structure ol li{
	list-style-type: none;
	padding: 8px 0
}
.tree-structure ol li .check{
	position: relative;
}
.tree-structure ol li label{
	color: #909090;
	font-weight: normal;
}
.tree-structure .check{
	background-color: #666;
	min-width: 24px;
	padding-left: 0px;
	padding-right: 0px;
	text-align: center;
	padding: 3px 9px;
	margin-right: 10px;
	color: #fff;
	font-weight: 700;
	font-size: 12px;
	display: inline-block;
	vertical-align: middle;
	border-radius:10px;
}
.tree-structure  ol  li .check:before{
	position: absolute;
	content: "";
	top: 0;
	bottom: 0;
	right: 100%;
	margin: auto;
	width: 33px;
	height: 1px;
	background-color: #939393;
}

